import { StopEstimate } from '@/api/translink-rtti-api';

export interface ScheduledBus {
	destination: string,
	expectedLeaveTime: string,
	expectedCountdown: number,
	status: string,
	lastUpdate: string
}

export interface ScheduledBuses {
	routeNo: string,
	stopNo: number,
	list: ScheduledBus[]
}

export class ScheduledBus {

	public static fromApiResponse(response: StopEstimate.Response): ScheduledBus[] {
		let list = [] as ScheduledBus[];
		for (let schedule of response.Schedules) {
			let status = '';
			if (schedule.CancelledStop || schedule.CancelledTrip) {
				status = 'Cancelled';
			} else if (schedule.ExpectedCountdown < 0) {
				status = 'Departed';
			} else {
				switch (schedule.ScheduleStatus) {
					case ' ': status = 'On Time'; break;
					case '*': status = 'Scheduled'; break;
					case '+': status = 'Early'; break;
					case '-': status = 'Delayed'; break;
				}
			}
			list.push({
				destination: schedule.Destination,
				expectedLeaveTime: schedule.ExpectedLeaveTime.split(' ')[0],
				expectedCountdown: schedule.ExpectedCountdown,
				status,
				lastUpdate: schedule.LastUpdate
			});
		}
		return list.sort((x, y) => x.expectedCountdown - y.expectedCountdown);
	}

}