
import { TranslinkRttiApi } from '@/api/translink-rtti-api';
import StopSchedule from '@/components/stop-schedule.vue';
import { ScheduledBus, ScheduledBuses } from '@/models/scheduled-bus';
import { StopEstimateFilter } from '@/models/stop-estimate-filter';
import { Options, Vue } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';

@Options({
	components: {
		StopSchedule
	}
})
export default class StopEstimate extends Vue {

	@Prop()
	apiKey?: string;

	filter: StopEstimateFilter[] = [
		{
			routeNo: '016',
			stopNo: 51042,
			timeFrame: 60
		},
		{
			routeNo: '009',
			stopNo: 50907,
			timeFrame: 60
		}
	];

	stops = [] as ScheduledBuses[];

	mounted() {
		this.load();
	}

	load() {
		this.filter.forEach(async (stop, i) => 
			this.stops[i] = {
				routeNo: stop.routeNo!,
				stopNo: stop.stopNo!,
				list: ScheduledBus.fromApiResponse(
					await TranslinkRttiApi.stopEstimate({
						apiKey: this.apiKey!,
						routeNo: stop.routeNo!,
						stopNo: stop.stopNo!,
						timeFrame: 60
					})
				)
			}
		);
	}

}
