export namespace StopEstimate {

	export interface Request {
		apiKey: string,
		routeNo: string,
		stopNo: number,
		count?: number,
		timeFrame?: number
	}

	export interface Response {
		RouteNo: string,
		Schedules: Schedule[]
	}

	export interface Schedule {
		AddedStop: boolean,
		AddedTrip: boolean,
		CancelledStop: boolean,
		CancelledTrip: boolean,
		Destination: string
		ExpectedCountdown: number,
		ExpectedLeaveTime: string,
		LastUpdate: string,
		Pattern: string,
		ScheduleStatus: string
	}

}

const headers: RequestInit = {
	headers: {
		'Accept': 'application/json',
		'Content-Type': 'application/json'
	}
};

export class TranslinkRttiApi {

	public static async stopEstimate(request: StopEstimate.Request): Promise<StopEstimate.Response> {
		let url = `https://api.translink.ca/rttiapi/v1/stops/${request.stopNo}/estimates?apikey=${request.apiKey}&routeNo=${request.routeNo}`;

		if (request.count !== undefined) 
			url += `&count=${request.count}`;
		if (request.timeFrame !== undefined) 
			url += `&timeframe=${request.timeFrame}`;

		try {
			let response = await fetch(url, headers);
			let json = await response.json();
			if (response.ok) {
				return json[0] as StopEstimate.Response;
			}
			throw new Error(json);
		} catch (e) {
			throw e;
		}
	}

}